import { rules } from '../../../.eslintrc';
<template>
  <div class="user-container">
    <!-- 面包屑导航 -->
    <Breadcrumb msg1='用户管理' msg2='角色管理'></Breadcrumb>
    <!-- 卡片视图 -->
    <el-card class="box-card">
      <!-- 搜索框 -->
      <div class="search">
          <el-input
            placeholder="请输入内容"
            v-model="paramsObj.query"
            class="input-with-select"
          >
            <el-button @click="getUsers" slot="append" icon="el-icon-search"></el-button>
          </el-input>
          <el-button type="primary" @click='addUser'>添加用户</el-button>
      </div>
      <!-- 表格 -->
      <el-table border :data="tableData" fit style="width: 100%">
          <el-table-column label="#" type="index" width="50"></el-table-column>
          <el-table-column label="姓名" width="180" prop="username"></el-table-column>
          <el-table-column label="邮箱" width="180" prop="email"></el-table-column>
          <el-table-column label="电话" width="180" prop="mobile"></el-table-column>
          <el-table-column label="角色" width="180" prop="role_name"></el-table-column>
          <el-table-column label="状态" width="180">
            <template slot-scope="scoped">
              <el-switch
                v-model="scoped.row.mg_state"
                active-color="#13ce66"
                inactive-color="#ff4949"
                 @change="changeStatus(scoped.row)"
              >
              </el-switch>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-edit"
                @click.native.prevent="editorRow(scope.row)"
              ></el-button>
              <el-button
                size="mini"
                type="danger"
                icon="el-icon-delete"
                @click.native.prevent="deleteRow(scope.row)"
              ></el-button>
              <el-button
                size="mini"
                type="warning"
                icon="el-icon-setting"
                @click.native.prevent="roleRow(scope.row)"
              ></el-button>
            </template>
          </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="paramsObj.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
      </el-pagination>
    </el-card>
    <!-- 添加用户弹出框 -->
    <el-dialog title="添加用户" :visible.sync="dialogVisible" width="50%" class="demo-ruleForm">
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="70px">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input v-model="ruleForm.password"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop='email'>
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="ruleForm.mobile"></el-input>
        </el-form-item>
        <el-form-item class="submit-box">
          <el-button @click="resetForm('ruleForm')" >取消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 修改用户弹出框 -->
    <el-dialog title="修改用户" :visible.sync="editor" width="50%" class="demo-ruleForm">
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="70px">
        <el-form-item label="用户名">
          <el-input v-model="ruleForm.username" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop='email'>
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="手机" prop="mobile">
          <el-input v-model="ruleForm.mobile"></el-input>
        </el-form-item>
        <el-form-item class="submit-box">
          <el-button @click="editor = false" >取消</el-button>
          <el-button type="primary" @click="editorForm('ruleForm')">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 分配角色 -->
    <el-dialog title="分配角色" :visible.sync="role" width="50%" class="demo-ruleForm">
      <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="70px">
       <p>当前的用户：{{ruleForm.username}}</p>
       <p>当前的角色：{{ruleForm.role_name}}</p>
       分配新角色：
       <el-select v-model="ruleForm.region" placeholder="请选择">
         <el-option :label="item.roleName" :value="item.id" v-for="(item,index) in roles" :key='index'></el-option>
       </el-select>

        <el-form-item class="submit-box">
          <el-button @click="role = false" >取消</el-button>
          <el-button type="primary" @click="roleForm(ruleForm.region)">确认</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: '',
  data () {
    // 手机号验证
    const reg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/
    var validatorPhone = function (rule, value, callback) {
      if (value === '') {
        callback(new Error('请输入手机号'))
      } else if (!reg.test(value)) {
        callback(new Error('请输入合法的手机号'))
      } else {
        callback()
      }
    }
    return {
      select: '',
      tableData: [],
      total: 0,
      paramsObj: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      /* 分页 */
      currentPage4: 1,
      // 添加弹出框
      dialogVisible: false,
      // 编辑弹出框
      editor: false,
      // 分配用户角色
      role: false,
      // 分配新角色列表
      roles: [],
      ruleForm: {
        username: '',
        password: '',
        email: '',
        mobile: '',
        role_name: '',
        region: ''
      },
      // 用户ID（编辑和分配角色时使用）
      Id: '',
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ],
        mobile: [
          { required: true, validator: validatorPhone, trigger: 'blur' }
        ]
      }

    }
  },
  created () {
    this.getUsers()
  },
  methods: {
    /* 获取用户 */
    async getUsers () {
      const res = await this.$http.get('/users', {
        params: this.paramsObj
      })
      this.tableData = res.data.data.users
      this.total = res.data.data.total
    },
    /* 分页 */
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.paramsObj.pagesize = val
      this.getUsers()
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`)
      this.paramsObj.pagenum = val
      this.getUsers()
    },
    // 添加用户
    addUser () {
      this.ruleForm = {}
      this.dialogVisible = true
    },
    // 添加弹出框取消按钮
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.dialogVisible = false
    },
    // 添加弹出框确定按钮
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // alert('submit!')
          const res = await this.$http.post('/users', this.ruleForm)
          if (res.data.meta.status === 201) {
            this.$message({
              message: '创建成功',
              type: 'success'
            })
            this.getUsers()
            this.resetForm(formName)
          } else if (res.data.meta.status === 400) {
            this.$message({
              showClose: true,
              message: '用户名已存在，请重新输入',
              type: 'warning'
            })
            this.ruleForm.username = ''
          } else {
            this.$message.error('创建失败')
            this.resetForm(formName)
          }
        } else {
          return false
        }
      })
    },
    // 改变状态
    async changeStatus (rows) {
      const res = await this.$http.put(`/users/${rows.id}/state/${rows.mg_state}`)
      if (res.data.meta.status === 200) {
        this.$message({
          message: '更改用户状态成功',
          type: 'success'
        })
      }
    },
    // 修改用户
    async editorRow (msg) {
      this.editor = true
      this.ruleForm = msg
      this.Id = msg.id
    },
    // 修改用户的确认按钮
    editorForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const res = await this.$http.put('/users/' + this.Id, {
            email: this.ruleForm.email,
            mobile: this.ruleForm.mobile
          })
          if (res.data.meta.status === 200) {
            this.$message({
              message: '更新用户信息成功',
              type: 'success'
            })
            this.Id = ''
            this.getUsers()
            this.editor = false
          }
        } else {
          return false
        }
      })
    },
    // 删除
    deleteRow (msg) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const res = await this.$http.delete('/users/' + msg.id)
        if (res.data.meta.status === 200) {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getUsers()
        } else {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 分配角色
    async roleRow (msg) {
      this.Id = msg.id
      this.role = true
      this.ruleForm = msg
      const res = await this.$http.get('/roles')
      this.roles = res.data.data
    },
    async roleForm (id) {
      const res = await this.$http.put('/users/' + this.Id + '/role', {
        rid: id
      })
      if (res.data.meta.status === 200) {
        this.$message({
          type: 'success',
          message: '更新角色成功!'
        })
        this.ruleForm.region = ''
        this.role = false
        this.getUsers()
      } else {
        this.$message({
          type: 'info',
          message: '更新角色失败'
        })
      }
    }
  }
}
</script>
<style lang='less'  scoped>
.user-container {
  .box-card {
    margin-top: 20px;
    .search {
      display: flex;

      .el-input {
        width: 40%;
        margin-right: 20px;
      }
    }
    /* 表格 */
    .el-table {
      margin: 20px 0;
    }
  }
}
/deep/.el-dialog__body {
  padding-bottom: 5px;
  .submit-box{
    margin-top: 50px;
    text-align: right;
  }
}
p{
  margin-bottom: 20px;
}
</style>
